import React, { useState, useEffect } from 'react';
import { auth, isPasswordValid } from '../firebase';
import { createUserWithEmailAndPassword, updateProfile, sendEmailVerification } from 'firebase/auth';

function SignUpBox({ onGoBack, onSignUpSuccess, closeAllPopups }) {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(null);
  const [isEmailVerified, setIsEmailVerified] = useState(false);
  const [verificationMessage, setVerificationMessage] = useState('');
  const [Neutralmessage, setNeutralMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [loadingDots, setLoadingDots] = useState(0); // Added loadingDots state

  const clearError = () => {
    setError(null);
  };


  const checkEmailVerification = () => {
    const checkForVerifiedInterval = setInterval(() => {
      auth.currentUser
        .reload()
        .then(() => {
          if (auth.currentUser.emailVerified) {
            clearInterval(checkForVerifiedInterval);
            setIsEmailVerified(true);
            setVerificationMessage('Email successfully verified');
            
            // Add a 2-second delay before closing the popup
            setTimeout(() => {
              onSignUpSuccess(); // Call the success callback if needed
              setLoading(false); // Turn off loading when email is verified
              closeAllPopups(); // Close the popup after the delay
            }, 2000);
          }
        })
        .catch((error) => {
          console.error('Error reloading user:', error);
        });
    }, 1000);
  
    return () => clearInterval(checkForVerifiedInterval);
  };
  

  // Start the loading dots animation when loading is true
  useEffect(() => {
    let dotInterval;

    if (loading) {
      dotInterval = setInterval(() => {
        setLoadingDots((prevDots) => (prevDots + 1) % 4);
      }, 500);
    } else {
      clearInterval(dotInterval);
      setLoadingDots(0);
    }

    return () => {
      clearInterval(dotInterval);
    };
  }, [loading]);
  const handleSignUp = () => {
    clearError();
    setLoading(true);
  
    if (!firstName || !lastName || !email || !password) {
      setError('Please fill in all fields.');
      setLoading(false);
      return;
    }
  
    // Check if the password meets your custom requirements
    if (!isPasswordValid(password)) {
      setError(
        'Password must meet the following requirements:\n' +
        '- At least 8 characters\n' +
        '- At least one lowercase letter\n' +
        '- At least one uppercase letter\n' +
        '- At least one digit\n' +
        '- At least one special character (@$!%*?&)'
      );
      setLoading(false);
      return;
    }

    const profileImageURLs = [
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-fire.webp?alt=media&token=1a7a4ce6-9bc6-4061-923c-19adab65a324',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-green.webp?alt=media&token=f499dfa7-41c8-4a86-a8a9-d631bfdf521c',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-greenish.webp?alt=media&token=1f7b8694-92f4-42db-8063-fa161df20845',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-meadow.webp?alt=media&token=3ef4ea4c-9584-4b1c-a05e-f18ad4565aa1',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-pinkish.webp?alt=media&token=41b3bbe3-43e6-439c-be75-18fc3307b164',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-purple.webp?alt=media&token=aa183b64-1a65-4199-970d-9c0b8b8398f3',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-red.webp?alt=media&token=38e75119-ddfb-47c0-8cef-87738d8cefa0',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-regal.webp?alt=media&token=c604fb39-e2a0-440a-956b-f4d71eea15d3',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-sea.webp?alt=media&token=dc0049a6-cab2-4fe7-8c74-41c38601dcdb',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-space.webp?alt=media&token=2202be2e-9552-4033-ada5-29230280ff49',
      'https://firebasestorage.googleapis.com/v0/b/tio-login-auth.appspot.com/o/profileimages%2Ftio-sun.webp?alt=media&token=956f9e88-a313-43bf-b520-57afa081b2fd',
    ]
    
    
    const randomIndex = Math.floor(Math.random() * profileImageURLs.length);
    const selectedProfileImageURL = profileImageURLs[randomIndex];

  
  
    createUserWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        const fullName = `${firstName} ${lastName}`;

        // Set the user's display name and photoURL
        updateProfile(user, {
          displayName: fullName,
          photoURL: selectedProfileImageURL,
        })
          .then(() => {
            sendEmailVerification(user)
              .then(() => {
                setNeutralMessage(
                  'Verification email sent. Please check your inbox and do not leave this page.'
                );
                checkEmailVerification();
                setError(null); // Clear any previous error
              })
              .catch((error) => {
                console.error('Error sending verification email:', error);
                setError('An error occurred while sending the verification email.');
                setLoading(false);
              });
          })
          .catch((error) => {
            console.error('Error updating profile:', error);
            setError('An error occurred during profile update');
            setLoading(false);
          });
      })
      .catch((error) => {
        console.error('Error creating user:', error);
        setError('An error occurred during sign-up.');
        setLoading(false);
        setVerificationMessage(''); // Clear any previous success message
        if (error.code === 'auth/email-already-in-use') {
          setError('Email is already in use. Please use a different email address.');
        } else {
          setError('An error occurred during sign-up.');
        }
      });
  };
    

  const handleInputKeyDown = (event) => {
    if (event.key === 'Enter') {
      handleSignUp();
  
      const submitButton = document.getElementById('btnSignup');
      if (submitButton) {
        submitButton.focus();
      }
    }
  };

  const capitalizeFirstLetter = (input) => {
    return input.charAt(0).toUpperCase() + input.slice(1);
  };

  useEffect(() => {
    if (auth.currentUser) {
      checkEmailVerification();
    }
  }, [onSignUpSuccess]);


  useEffect(() => {
    const checkIsMobile = () => {
      setIsMobile(window.innerWidth <= 768); // Adjust the breakpoint as needed
    };

    // Initial check
    checkIsMobile();

    // Add event listener for window resize to update isMobile
    window.addEventListener('resize', checkIsMobile);

    // Clean up the event listener
    return () => {
      window.removeEventListener('resize', checkIsMobile);
    };
  }, []);


  return (
    <div>
      <div className="login-box" id="loginBox">
        <div className="log-in-text">
          <div className="opening-second">
            <div className="go-back">
              <button className="go-back-button" onClick={onGoBack}>
                <i className="fas fa-backspace"></i>
              </button>
              <div className="center-text">
                <h4 className="log-in-text-heading">Sign-Up</h4>
              </div>
            </div>
          </div>
          <h6 className="log-in-text-closing">
      <p className="fw-light">Let's sign you up so you can get the answers you need!</p>
    </h6>

  {isMobile && (
    <img
      src={`${process.env.PUBLIC_URL}/images/logo/Logo-dark.webp`}
      alt="TioAssist logo"
      width="100"
      style={{
        position: 'absolute',
        top: 10,
        left: 10,
      }}
    />
  )}

        </div>
        <form id="signupForm">
          <div className="signup-form">
          <div className="col-lg-12 col-md mb-3">
            <input
              type="text"
              placeholder="First Name"
              id="input-FirstName"
              className="shadow form-control form-control-lg"
              value={firstName}
              onChange={(e) => setFirstName(capitalizeFirstLetter(e.target.value))} // Capture user's input for first name and capitalize
              onKeyDown={handleInputKeyDown} // Submit on Enter
            />
          </div>
          <div className="col-lg-12 col-md mb-3">
            <input
              type="text"
              placeholder="Last Name"
              id="input-LastName"
              className="shadow form-control form-control-lg"
              value={lastName}
              onChange={(e) => setLastName(capitalizeFirstLetter(e.target.value))} // Capture user's input for last name and capitalize
              onKeyDown={handleInputKeyDown} // Submit on Enter
            />
          </div>
            <div className="col-lg-12 mb-3">
              <input
                type="email"
                placeholder="email address"
                id="input-Email"
                className="shadow form-control form-control-lg"
                autoComplete="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                onKeyDown={handleInputKeyDown} // Submit on Enter
              />
            </div>
            <div className="col-lg-12 mb-3">
              <input
                type="password"
                placeholder="password"
                id="input-Password"
                className="shadow form-control form-control-lg"
                autoComplete="new-password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                onKeyDown={handleInputKeyDown} // Submit on Enter
              />
            </div>
         
            <div className="col-lg-12 col-md mb-3" style={{ paddingBottom: '15px'}}>
  {!isEmailVerified && ( // Only show the button if not email verified
    <button
      type="button"
      className="signup-button"
      onClick={handleSignUp}
      id="btnSignup"
      disabled={loading}
    >
      <span>
        {loading ? 'Signing up' : 'Submit'}
      </span>
      {loading && (
        <span className="loading-dots">
          {Array(loadingDots + 1).join('.')}
        </span>
      )}
    </button>
  )}
</div>



<div>
  {error && <p style={{ color: 'red' }}>{error}</p>}
  {verificationMessage && (
    <p style={{ color: 'green' }}>{verificationMessage}</p>
  )}
  {!verificationMessage && Neutralmessage && (
    <p>{Neutralmessage}</p>
  )}
</div>

          </div>
        </form>
        <div role="separator" className="terms-divider"></div>
<div className="terms">
<p className="fw-light">
By continuing, you agree to our <a href="URL_TO_TERMS_OF_USE" target="_blank" rel="noopener">Terms of Use</a>.
Read our <a href="URL_TO_PRIVACY_POLICY" target="_blank" rel="noopener">Privacy Policy</a>.
</p>
</div>
      </div>
    </div>
  );
}

export default SignUpBox;