import React, { useState, useRef, useEffect } from 'react';
import { getAuth, sendPasswordResetEmail } from "firebase/auth";


function PasswordBox({ onGoBack }) {
  const [email, setEmail] = useState("");
  const [resetSuccess, setResetSuccess] = useState(false);
  const [resetError, setResetError] = useState(null);
  const resetButtonRef = useRef(null);
  const [isMobile, setIsMobile] = useState(false);

  const handleResetPassword = () => {
    const auth = getAuth();

    sendPasswordResetEmail(auth, email)
      .then(() => {
        // Password reset email sent successfully
        setResetSuccess(true);
        setResetError(null);
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        // Handle the error, you can display an error message to the user
        setResetSuccess(false);
        setResetError(errorMessage);
      });
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleResetPassword();

      // Set focus on the submit button
      const submitButton = document.getElementById('resetButton');
      if (submitButton) {
        submitButton.focus();
      }
    }
  };

  useEffect(() => {
    const checkIsMobile = () => {
      setIsMobile(window.innerWidth <= 768); // Adjust the breakpoint as needed
    };

    // Initial check
    checkIsMobile();

    // Add event listener for window resize to update isMobile
    window.addEventListener('resize', checkIsMobile);

    // Clean up the event listener
    return () => {
      window.removeEventListener('resize', checkIsMobile);
    };
  }, []);


  return (
<div className="login-box" id="loginBox">
  <div className="log-in-text">
    <div className="opening-second">
      <div className="go-back">
        <button className="go-back-button" onClick={onGoBack}>
          <i className="fas fa-backspace"></i>
        </button>
        <div className="center-text">
          <h4 className="log-in-text-opening">New password?</h4>
        </div>
      </div>
    </div>
    <h6 className="log-in-text-closing">
      <p className="fw-light">Please type the email address associated with your account</p>
    </h6>  


  {isMobile && (
    <img
      src={`${process.env.PUBLIC_URL}/images/logo/Logo-dark.webp`}
      alt="TioAssist logo"
      width="100"
      style={{
        position: 'absolute',
        top: 10,
        left: 10,
      }}
    />
  )}

    
    
    
    
    </div>

  <div className="signup-form">
        <div className="col-lg-12 mb-3 mt-5">
          <input
            type="email"
            placeholder="email address"
            id="request-NewPassword"
            className="shadow form-control form-control-lg"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            onKeyDown={handleKeyPress} // Call handleKeyPress on key press
          />
        </div>
      </div>

      <div className="col-lg-12 mb-3 mt-5">
      <button
          id="resetButton"
          className="signup-button"
          type="button"
          onClick={handleResetPassword}
          onKeyDown={handleKeyPress} // Listen for "Enter" key press
        >
          Submit
        </button>
      </div>

      {resetSuccess && (
        <div>
          <p className="fw-light" style={{ color: 'green' }}>
      <span
        onClick={onGoBack}
        style={{ color: 'green', cursor: 'pointer' }}
      >
        Password reset email sent successfully! Click here to login!
      </span>
    </p>
        </div>
      )}
      {resetError && (
        <p  className="fw-light"  style={{ color: 'red' }}>The email adress is wrong or does not exist.</p>
      )}

<div role="separator" className="terms-divider"></div>
<div className="terms">
<p className="fw-light">
By continuing, you agree to our <a href="URL_TO_TERMS_OF_USE" target="_blank" rel="noopener">Terms of Use</a>.
Read our <a href="URL_TO_PRIVACY_POLICY" target="_blank" rel="noopener">Privacy Policy</a>.
</p>
</div>
    </div>
  );
}

export default PasswordBox;
